export default {
    
    fetch: {
        fetch(path, callback){
            // const url = 'https://eia.moe/d/';
            const url = "https://eia-moe.github.io/map-dist/d/";
            const request = require('request');
            const options = { json: true };
            const fullUrl = `${url}${path}`;
            request(fullUrl, options, callback);
        },

        fetchData(name){
            return new Promise((resolve, reject) =>
                this.fetch(name, (error, res, body) => {
                    if (error) { reject(error); };
                    if (!error && res.statusCode == 200) {
                        resolve(body);
                    }else{
                        reject(body);
                    }
                    
                })
            );
        },

        projectsAsync(){ return this.fetchData('projects'); },
        riverAsync(){ return this.fetchData('mekong'); },
        khetAsync(){ return this.fetchData('khet'); }
    }
    
}